import { Db, MongoClient, MongoClientOptions } from 'mongodb';

export type TMongoConnectionlOptions = {
  user?: string;
  password?: string;
  dbName?: string;
  host?: string;
};

export type TMongoCommandOptions = {
  docName: string;
};

export class MongoAdapter {
  client?: MongoClient;

  db?: Db;

  options: MongoClientOptions;

  connectionString: string;

  constructor(
    url: string | TMongoConnectionlOptions,
    options: MongoClientOptions = {}
  ) {
    if (typeof url === 'object') {
      const { user, password: pwd, dbName: db, host } = url;
      // eslint-disable-next-line no-param-reassign
      url = user ? `mongodb://${user}:${pwd}@${host}` : `mongodb://${host}`;
      // eslint-disable-next-line no-param-reassign
      url += db ? `/${db}` : '';
    }
    this.connectionString = url;
    this.options = options;
  }

  async connect() {
    if (this.db) return this.db;
    const client = await MongoClient.connect(
      this.connectionString,
      this.options
    );
    this.db = client.db();
    return this.db;
  }

  async collection(name: string) {
    const client = await this.connect();
    return client.collection(name);
  }

  async get<T extends TMongoCommandOptions>(query: T) {
    const doc = await this.collection(query.docName);
    return doc.findOne(query);
  }

  async put<T extends TMongoCommandOptions>(query: T, values: object) {
    const collection = await this.collection(query.docName);
    const doc = await collection.findOneAndUpdate(
      query,
      { $set: { ...query, ...values } },
      { upsert: true, returnDocument: 'after' }
    );
    if (!doc) return;
    return doc['value'];
  }

  async del<T extends TMongoCommandOptions>(query: T) {
    const doc = await this.collection(query.docName);
    return doc.deleteMany(query);
  }

  async readAsCursor<T extends TMongoCommandOptions>(
    query: T,
    { limit, reverse }: { limit?: number; reverse?: boolean } = {}
  ) {
    const doc = await this.collection(query.docName);
    let curs = doc.find(query);
    if (reverse) curs = curs.sort({ clock: -1 });
    if (limit) curs = curs.limit(limit);
    return curs.toArray();
  }

  /**
   * Close connection to MongoDB instance.
   */
  close() {
    this.client?.close();
  }

  async getCollectionNames() {
    const collections = (await this.db?.listCollections().toArray()) ?? [];
    return collections.map(({ name }) => name);
  }

  /**
   * Delete database
   */
  async flush() {
    await this.db?.dropDatabase();
    await this.client?.close();
  }

  async dropCollection(collectionName: string) {
    const doc = await this.collection(collectionName);
    return doc.drop();
  }
}
